import { createRouter, createWebHistory } from "vue-router";
import HomeView from "../views/HomeView.vue";
import MyPortafolio from "../views/MyPortafolio.vue";
import AboutView from "../views/AboutView.vue";
import MyBlogs from "../views/MyBlogs.vue";
import MyContact from "../views/MyContact.vue";

const routes = [
  {
    path: "/",
    name: "home",
    component: HomeView,
  },
  {
    path: "/portafolio/portafolio",
    name: "portafolio",
    component: MyPortafolio,
  },
  {
    path: "portafolio/about",
    name: "about",
    component: AboutView,
  },
  {
    path: "portafolio/blogs",
    name: "blogs",
    component: MyBlogs,
  },
  {
    path: "portafolio/contact",
    name: "contact",
    component: MyContact,
  },
];

const router = createRouter({
  history: createWebHistory(process.env.BASE_URL),
  routes,
});

export default router;
